
export default class Viz1 {
  constructor(canvas) {
    this.canvas = canvas;
    this.ctx = canvas.getContext("2d");
    this.items = 512;
    this.updateCanvasSizes();
    window.addEventListener('resize', this.updateCanvasSizes);
    this.ignoreOnHigh = 80;
    this.ignoreOnLow = 4;
    canvas.style.backgroundImage = ``;
  }

  updateCanvasSizes = () => {
    this.canvas.width = this.canvas.clientWidth;
    this.canvas.height = this.canvas.clientHeight; 
    this.WIDTH = this.canvas.width; 
    this.HEIGHT = this.canvas.height;
    this.needUpdateSizes = true;
  }


  updateSizes = () => {
    this.aRatio = this.WIDTH / this.HEIGHT;
    this.itemsInRow = Math.floor(Math.sqrt( this.aRatio *  this.items));
    this.rows = Math.floor( this.items/ this.itemsInRow);
    this.radius = ( this.WIDTH/ this.itemsInRow)/2;
    this.needUpdateSizes = false;
  }

  draw = (dataArray) => {
    const bufferLength = dataArray.length;
    if (this.needUpdateSizes) this.updateSizes();

    this.ctx.clearRect(0, 0, this.WIDTH, this.HEIGHT);
    const iStep = (bufferLength - (this.ignoreOnHigh + this.ignoreOnLow)) / this.items;
    let x = this.radius;
    let y = this.HEIGHT - this.radius;
    let nowInRow = 0;
    for (let i = this.ignoreOnLow; i < bufferLength - this.ignoreOnHigh; i+=iStep) {
      i = Math.round(i);
      this.ctx.fillStyle = 'rgb(' + (dataArray[i]) + ',' + 50 + ',' + (i / bufferLength) * 355 + ')';
      this.ctx.beginPath();
      this.ctx.arc(x,y, this.radius*dataArray[i]/256, 0, 2 * Math.PI);
      this.ctx.closePath();
      this.ctx.fill();
      nowInRow++;
      if(nowInRow >= this.itemsInRow) {
        nowInRow = 0;
        y-=this.radius*2;
        x = this.radius;
      } else {
        x+=this.radius*2;
      }
    }
}
}