import React, {Component} from 'react';
import './Timeline.scss';

class Timeline extends Component {

  clickHandler = (event) => {
    var bounds = event.target.getBoundingClientRect();
    var width = event.target.offsetWidth;
    var x = event.clientX - bounds.left;
    this.props.onTimelineClick(x/width);
  }

  getMMSSfromS = (time) => {
    let s =~~ time; 
    return (s - (s %= 60)) / 60 + (9 < s ? ':' : ':0') + s;
  }

  render () {
    const translate = (this.props.position / this.props.duration) * 100;
    const currentTime = this.getMMSSfromS(this.props.position);
    const fullTime = this.getMMSSfromS(this.props.duration);

    return (
      <div className="timeline">
        <div className="timeline-inner" style={{transform: `translateX(${translate}%)`}} ></div>
        <div className="timeline-over"  onClick={this.clickHandler}></div>
        <p className="timeline-time">{`${currentTime}/${fullTime}`}</p>
      </div>
    );}
}  

export default Timeline;