import React, { Component } from 'react';

import Timeline from './Timeline/Timeline';
import DropZone from './DropZone/Dropzone';
import Vizualizations from './Vizualization';
import Volume from './Volume/Volume';

import barsSvg from '../../assets/vizual-icons/bars.svg';
import singerBg from '../../assets/singer.jpg';
import track from '../../assets/music/demo.mp3';

import './mz.scss';

class Music extends Component {
  constructor(props){
    super(props);
    this.audioCtx = new(window.AudioContext || window.webkitAudioContext)();
    this.analyser = this.audioCtx.createAnalyser();
    this.gainNode = this.audioCtx.createGain();
    this.gainNode.connect(this.audioCtx.destination)
    this.analyser.fftSize = 2048;
    this.bufferLength = this.analyser.frequencyBinCount;
    this.dataArray = new Uint8Array(this.bufferLength);
    this.sourceNode = null;
  }

  state = {
    actionAt: 0,
    playing: false,
    buffer: null,
    position: 0,
    drawing: false,
    loading: false,
    loadingFileName: 'DEMO: Lack Labour - Let Me [cut]',
    currentFileName: '',
    currentVizIndex: 0,
    vizChanged: false
  }

  componentDidMount () {
    this.defaultAudioPlay();
  }

  getCanvasRef = e => {this._canvasRef = e};

  createSourceNode = () => {
    if (!this.state.buffer) return;
    this.sourceNode = this.audioCtx.createBufferSource();
    this.sourceNode.connect(this.analyser);
    this.sourceNode.connect(this.gainNode)
    this.sourceNode.buffer = this.state.buffer;
  }

  defaultAudioPlay = () => {
    this.setState({loading: true});

    fetch(track).then((res)=>{
      return(res.blob());
    }).then((file)=>{
        this.getArrayBuffer(file);
    }).catch((e) => {
      this.setState({loading: false});
    })
  }

  fileDropHandler = (file) => {
    if (!file) {
      return;
    }
    this.setState({loading: true});
    this.getArrayBuffer(file);
  }

  parseFileName = (fileName) => {
    const name = fileName.replace(/.mp3|.ogg|.aac|.wav|.ogg|.mpeg|.opus|.oga|.caf|.m4a|.mp4|.weba|.dolby|.flac/g,'');
    return name;
  }

  getArrayBuffer = (file) => {
    if(file.name) {
      const parsedFileName = this.parseFileName(file.name); 
      this.setState({loadingFileName: parsedFileName});
    }

    const fileReader = new FileReader();

    fileReader.onload = (e) => {
      const arrayBuffer = e.target.result;
      this.loadTrackFromArrayBuffer(arrayBuffer);
    };

    fileReader.readAsArrayBuffer(file);
  }

  loadTrackFromArrayBuffer = (arrayBuffer) => {
    this.audioCtx.decodeAudioData(arrayBuffer, (buffer) => { 
      let startPlayAfterLoading = false;
      if(this.state.playing) startPlayAfterLoading = true;
      this.stopPlaying();
      const currentFileName = this.state.loadingFileName;
      this.setState({buffer, loading:false, currentFileName, loadingFileName: ''});
      if(startPlayAfterLoading) this.startPlaying();
    }, function (error) {
      console.log(error);
      this.setState({loading:false})
    });
  }
  
  
  startPlaying = (position) => {
    this.createSourceNode();
    if (!this.sourceNode) return;
    const pos = typeof position === 'number' ? position : this.state.actionAt || 0;
    const startedAt = this.audioCtx.currentTime - pos;
    this.sourceNode.start(this.audioCtx.currentTime, pos);
    this.draw();
    this.setState({playing: true, actionAt: startedAt})
    this.getPlayingPosition();
  }

  pausePlaying = () => {
    const pausedAt =  this.audioCtx.currentTime - this.state.actionAt;   
    this.stopPlaying(pausedAt);
  }

  stopPlaying = (position = 0) => {
    if (this.sourceNode) {          
      this.sourceNode.disconnect();
      this.sourceNode.stop(0);
      this.sourceNode = null;
    }
    this.setState({playing: false, actionAt: position, position});
  }
  
  getPlayingPosition = () => {
    if (!this.state.playing) return;
    let position = this.audioCtx.currentTime - this.state.actionAt;
    if ( position >= this.state.buffer.duration ) {
      position = this.state.buffer.duration;
      this.stopPlaying();
    }
    this.setState({position})
  }

  setPositionOnTimeline = (x) => {
    if (!this.state.buffer) return;
    const position =  (x*this.state.buffer.duration);
    if (this.state.playing) {
      this.stopPlaying();
      this.startPlaying(position);
    } else {
      this.stopPlaying(position);
    }
  }

  playHandler = () => {
    this.startPlaying();
  }

  pauseHandler = () => {
    this.pausePlaying();
  }

  stopHandler = () => {
    this.stopPlaying();
  }

  setVolume = (value) => {
    this.gainNode.gain.value = value;
  }

  changeVizualHandler = () => {
    const l = Vizualizations.length;
    let i = this.state.currentVizIndex;
    i++;
    if(i >= l) i = 0;
    this.setState({currentVizIndex: i, vizChanged: true});
  }
  
  draw = () => {
    if(this.state.drawing) {
      return;
    }

    this.setState({drawing: true});
    const canvas = this._canvasRef;
    let vis = new Vizualizations[this.state.currentVizIndex](canvas);
    const frame = () => {
      if(this.state.vizChanged) {
        vis = new Vizualizations[this.state.currentVizIndex](canvas);
        this.setState({vizChanged: false});
      }
      this.getPlayingPosition();
      this.analyser.getByteFrequencyData(this.dataArray);
      vis.draw(this.dataArray);
      if (this.state.playing) {
         requestAnimationFrame(frame);
      } else {
        this.setState({drawing: false});
      }
    }
    requestAnimationFrame(frame);
  }
  
  render () {
    return (
      <div>
        <div className="mz-screen">
          {this.state.currentFileName ? <h2 className="mz-trackname">{this.state.currentFileName}</h2> : null}
        <canvas
          ref={this.getCanvasRef}
          width={2048} height={512}
          style={{backgroundImage: `url(${singerBg})`}} className="mz-canvas">
        </canvas>
        </div>
        
        {this.state.buffer 
          ? <Timeline position={this.state.position}  duration={this.state.buffer.duration} onTimelineClick={this.setPositionOnTimeline}/> 
          : <div className="mz-timeline-placeholder"></div>}

        <div className = "mz-bottom">
        {this.state.buffer ? <div className="mz-control-panel">
        {!this.state.playing ? 
          <button onClick={this.playHandler} className="mz-controls mz-play"></button> :
          <button onClick={this.pauseHandler}  className="mz-controls mz-pause"></button>}
          <button onClick={this.stopHandler} className="mz-controls mz-stop"></button>
          <button onClick={this.changeVizualHandler} className="mz-change-visual">
            <img src={barsSvg} style={{width: '30px'}} alt=''></img>
          </button>
        </div> : null }
        {this.state.loading ? <div className="mz-loading"></div> :  <DropZone onDrop = {this.fileDropHandler}/>}
        </div>
        <Volume onSetVolume={this.setVolume}/>
      </div>
    );
  }
}

export default Music;