import React, { PureComponent } from 'react';
import Icon from './VolumeIcon';
import './Volume.scss';
class Volume extends PureComponent {

  state = {
    value: 100,
    iconType: 'max',
    muted: false,
    memVolume: 100
  }
  onInputChange = (e) => {
    const value = e.target.value;
    this.setVolume(value);
    
  }

  setVolume = (value) => {
     
    const iconType = this.getIconType(value);
    const gainValue = (value/100).toFixed(2);
    this.props.onSetVolume(gainValue);
    this.setState({value, iconType, muted:false});
  }

  getIconType = (value) => {
    if(value > 70) return 'max';
    if(value > 30) return 'mid';
    if(value > 0) return 'low';
    return 'zero';
  }

  muteHandler = () => {
    if(this.state.muted) {
      this.setVolume(this.state.value);
      this.setState({muted: false});
      return;
    }
    this.setState({muted: true, iconType: 'muted'});
    this.props.onSetVolume(0);
  }

  render () {
    console.log('v-rendere')
    return (
      <div className="mz-volume">
        <button className="mz-volume-button" onClick={this.muteHandler}><Icon type={this.state.iconType}/></button>
        <input className="mz-volume-input" type="range" onChange={this.onInputChange} value={this.state.value}></input>
      </div>
    );
  }
}

export default Volume;