import singer from '../../../assets/singer.jpg';

export default class Viz3 {
  constructor(canvas) {
    this.canvas = canvas;
    this.ctx = canvas.getContext("2d");
    this.updateCanvasSizes();
    window.addEventListener('resize', this.updateCanvasSizes);
    this.ignoreOnHigh = 80;
    this.ignoreOnLow = 4;
    this.barWidth = 10;
    this.offsetWidth = 2;
    this.barCount = 0;
    canvas.style.backgroundImage = `url(${singer})`;
  }

  updateCanvasSizes = () => {
    this.canvas.width = this.canvas.clientWidth;
    this.canvas.height = this.canvas.clientHeight; 
    this.WIDTH = this.canvas.width; 
    this.HEIGHT = this.canvas.height;
    this.needUpdateSizes = true;
  }

  
  updateSizes = () => {
    this.barCount = (this.WIDTH / (this.barWidth+this.offsetWidth));
  }

  draw = (dataArray) => {
    const bufferLength = dataArray.length;
    if (this.needUpdateSizes) this.updateSizes();
    this.ctx.clearRect(0, 0, this.WIDTH, this.HEIGHT);
    const {barWidth, offsetWidth, barCount} = this;
    const iStep = Math.floor((bufferLength - (this.ignoreOnHigh + this.ignoreOnLow)) / barCount);
    let barHeight;
    let x = 0;
    for (let i = this.ignoreOnLow; i < bufferLength - this.ignoreOnHigh; i+=iStep) {
      barHeight = dataArray[i] * (this.HEIGHT/256);
      this.ctx.fillStyle = 'rgb(' + (barHeight) + ',' + 50 + ',' + (i / bufferLength) * 355 + ')';
      this.ctx.fillRect(x, this.HEIGHT - barHeight, barWidth, barHeight);
      x += (barWidth + offsetWidth);
    }
  }
}